html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  user-select: none;
  font-size: 16px;
  line-height: 1.5;
  color: #000;
  background-image: radial-gradient(circle, #F3F9FB, #fcfcff, #f7faff, #f1f8ff, #eaf7ff, #dcf3ff, #cdeefe, #bdeafd, #a3e0fd, #87d7fd, #69ccfe, #42c2ff);
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100vh;
}

.main {
  flex-grow: 1;
  margin-top: 140px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1330px;
}

img {
  border-radius: 8px;
  object-fit: cover;
}

.loading {
  position: relative;
  top: 0;
  left: 0;
  background: url("../images/loading2.gif") center no-repeat;
}

.title {
  position: relative;
  margin-bottom: 2rem;
  padding-bottom: 0.5rem;
  color: #243f4d;
  transition: color 0.3s;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-clamp: 2;
  box-orient: vertical;
}

.title:hover {
  color: #125D98;
}

.title:before {
  content: "";
  position: absolute;
  border-bottom: 2px solid #243f4d;
  bottom: 0.25rem;
  left: 0;
  width: 70%;
  transform: translateX(-50%);
}

.title:after {
  content: "";
  position: absolute;
  border-bottom: 2px solid #243f4d;
  bottom: 0;
  left: 0;
  width: 35%;
  transform: translateX(-50%);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  margin-bottom: 1.38rem;
  line-height: 1.3;
  letter-spacing: 1px;
  color: #243f4d;
}

h1 {
  margin-bottom: 0;
  font-size: 3.052rem;
  line-height: 1;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
}

@media only screen and (max-width: 767px) {
  h1 {
    font-size: 2.441rem;
  }
}

h2 {
  font-size: 2.441rem;
  font-style: italic;
  text-shadow: 1px 0 0 rgba(0, 0, 0, 0.8);
}

@media only screen and (max-width: 767px) {
  h2 {
    font-size: 1.953rem;
  }
}

h3 {
  font-size: 1.953rem;
}

@media only screen and (max-width: 767px) {
  h3 {
    font-size: 1.563rem;
  }
}

h4 {
  font-size: 1.563rem;
}

@media only screen and (max-width: 767px) {
  h4 {
    font-size: 1.25rem;
  }
}

h5 {
  font-size: 1.25rem;
}

p,
span {
  color: #111111;
}

.up-wrapper {
  position: relative;
  margin: 0 auto;
  max-width: 1300px;
}

.up {
  position: absolute;
  right: 40px;
  bottom: 40px;
  z-index: 9;
  width: 32px;
  height: 32px;
  cursor: pointer;
}

.up__txt {
  position: absolute;
  top: 36px;
  font-size: 12px;
}

.up__icon {
  border-radius: 50%;
  max-width: 40px;
  max-height: 40px;
  fill: #000;
  background-color: #e5f4f3;
}

.up--animated {
  animation-name: "up";
  animation-duration: 2s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}

.created {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

@media only screen and (max-width: 479px) {
  .created {
    font-size: 10px;
  }
}

.created__icon {
  width: 28px;
  height: 28px;
}

@media only screen and (max-width: 479px) {
  .created__icon {
    width: 24px;
    height: 24px;
  }
}

.created__link {
  margin-left: 6px;
  color: #fff;
}

