}

.header.color .header__inner {
  box-shadow: 0px 10px 8px 0px rgba(34, 60, 80, 0.4);
  border-top: 0px;
}

.header-top {
  display: flex;
  align-items: center;
  min-height: 60px;
  background-color: #125D98;
}

@media only screen and (max-width: 767px) {
  .header-top {
