       url('../fonts/pt-sans-v16-latin_cyrillic-ext_cyrillic-italic.woff') format('woff'), /* Modern Browsers */
       url('../fonts/pt-sans-v16-latin_cyrillic-ext_cyrillic-italic.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/pt-sans-v16-latin_cyrillic-ext_cyrillic-italic.svg#PTSans') format('svg'); /* Legacy iOS */
}
